@font-face {
  font-family: 'Pribambas';
  font-weight: 400;
  src: local('Pribambas'),
    url('./Pribambas-Regular.eot'),
    url('./Pribambas-Regular.eot#iefix') format('eot'),
    url('./Pribambas-Regular.ttf') format('ttf'),
    url('./Pribambas-Regular.woff') format('woff'),
    url('./Pribambas-Regular.woff') format('woff');
}

@font-face {
  font-family: 'Calibri';
  src: local('Calibri');
  font-style: normal;
  font-weight: normal;
}
